.app-wrapper {
    width: 80%;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "h h"
                         "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    width: 100%;
    grid-area: c;
    background-color: beige;
}

